// theme colors
$theme-primary-color: #e31b6d;
$theme-secondary-color: rgba(5, 160, 160, 1);

// standard colors;
$standard-color-white: #ffffff;
$standard-color-black: #000000;

// theme
// $dark: #171717; 
$dark: #0d1117;
$light: #e5e5e5; 

$themes: ( 
  dark: ( 
    bg: $dark, 
    text: $light, 
  ), 
  light: ( 
    bg: $light, 
    text: $dark, 
  ), 
);
